.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.center{
  text-align: center;
}
.col-4{
  padding-left: 0!important;
  padding-right: 0!important;
}
.trangthai{
  color:white;
  padding: 1px;
  border-radius: 7%;
}
.kichhoat{
  background-color: #dc3545;
}
.an{
  background-color:darkgreen;
}
.right{
  float: right;
  padding: 0;
  margin: 0;
}
button{
  color: white!important;
}
.btn-icon{
  color: #dc3545!important;
  float: right;
  padding: 0;
  margin: 5px;
  border: none;
  border-radius: 50%;
}
label{
  font-weight: 700;
}

.sort .dropdown-toggle::after {
  display: none;
}
.sort .dropdown-item{
  cursor: pointer;
}